.home-section {
    position: relative;
    z-index: 0;
    background-image: linear-gradient(rgba(0, 0, 255, 0.5), rgba(255, 255, 0, 0.5));
    background-image: var(--image-gradient), url('http://upload.wikimedia.org/wikipedia/commons/thumb/e/e5/NASA_logo.svg/1237px-NASA_logo.svg.png');
    background-position: top center;
    background-repeat: no-repeat;
    padding-bottom: 30px !important;
    padding-top: 30px !important;
}

.home-content {
    padding: 9rem 0 2rem !important;
    color: whitesmoke;
    text-align: left;
}

.spaceLogo {
    display: flex;
    width: 20%; 
    height: 20%;
}

.home-body {
    display: flex; 
    flex-direction: row; 
    justify-content: center;
    align-items: center;
    font-family: 'Poppins', sans-serif;
    font-size: 40px;
    font-weight: 400;

}

.content {
    display: flex; 
    flex-direction: row; 
    justify-content: center;
    align-items: center;
    font-family: Helvetica;
    font-size: 30px;
    font-weight: 400;
    margin-left: auto;
    margin-right: auto;
}
 